.container {
    width: 98%;
    margin: auto;
}
  
@media screen and (min-width:768px) {
    .container {
        width: 70%;
    }
}
  
.detail {
    margin-top: 3rem;
    margin-bottom: 3rem;
    width: 100%;
    display: flex;
    flex-wrap: wrap;
}
  
.detail_image {
    width: 50%;
    height: 50%;
}
  
.content {
    width: 100%;
}

.description{
    text-align: justify;
}
  
@media screen and (min-width:768px) {
    .detail__image {
        width: 50%;
    }
    .content {
        padding-left: 3rem;
        width: 50%;
    }
}