@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Amatic+SC&display=swap);

:root {
    --primaryColor: #cff5c1;
    --mainBkrnd: #cff5c1;
    --mainBlack: #222;
    --mainGrey: #ececec;
    --mainSpacing: 0.1rem;
    --mainTransition: all 0.5s linear;
}

$title-font: "Amatic SC";
$body-font: "Open Sans";

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    color: var(--mainBlack);
    background: var(--mainBkrnd);
    font-family: $body-font, sans-serif;
}

/* --------------- NAVBAR ---------------- */
.navbar {
    position: sticky;
    top: 0;
    height: 60px;
    width: 100%;
    display: flex;
    align-items: center;
    background: #baddae;
    z-index: 1;
    box-shadow: 0 0 7px -1px #00000091;
}

.navbar-center {
    width: 100%;
    max-width: 1170px;
    margin: auto;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 1.5rem;
}

.nav-menu {
    color: var(--mainBlack);
    background-color: #baddae;
    z-index: 4;
    align-self: flex-start;
    list-style-type: none;
    font-family: "Amatic SC";
}

li {
    cursor: pointer;
    display: inline-block;
    font-size: x-large;
    font-weight: bold;
    padding: 0.5rem;
}

a {
    color: var(--mainBlack);
    text-decoration: none;
}

.contador {
    width: fit-content;
    background-color: var(--mainGrey);
    margin: auto;
    padding: 1rem;
    text-align: center;
}